body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url(/Starwars/static/media/starwars.e15c8e28.jpg) no-repeat center fixed;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}
@font-face {
  font-family: 'Starjedi';font-size: 200px;
  src: url(/Starwars/static/media/Starjedi.cc3019aa.ttf); /* IE9 Compat Modes */
  }
h1{
  font-family: 'Starjedi';
  font-weight: 100;

  color:green;
  
  
}
.MainCss{
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin-bottom: 100px;
}
@media only screen and (max-width: 500px){
	.MainCss{
		-webkit-flex-wrap: wrap;
		        flex-wrap: wrap;	}
}
#card{
	font-size: 11px;
	width: 200px;
	background-color: white ;

  margin: 30px;
  
  box-shadow: 0px 0px 9px 4px  #6b0000 ;
  -webkit-animation: glow 1.5s linear infinite alternate;
          animation: glow 1.5s linear infinite alternate;

}
@-webkit-keyframes glow{
  to {
    box-shadow: 0px 0px 30px 20px red;
    background-color: red; 
  }
}
@keyframes glow{
  to {
    box-shadow: 0px 0px 30px 20px red;
    background-color: red; 
  }
}
#planet{
	color:white;
}
